@import url(https://fonts.googleapis.com/css?family=Poppins:500,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.header {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(2);
  font-family: "Poppins", sans-serif; }
  .header__name {
    color: #35323e;
    font-weight: 700;
    grid-column: 2/3; }
  .header__name:hover {
    color: #232127;
    cursor: pointer; }
  .header__menu {
    display: none; }
  .header__navbar {
    grid-column: 3/12;
    display: flex;
    flex-direction: row;
    align-items: center; }
    .header__navbar__link {
      color: #9e9aa7;
      font-weight: 700;
      font-size: 16px;
      margin: 0 15px; }
      .header__navbar__link--right {
        margin-left: auto;
        margin-right: 10px; }
    .header__navbar__link:hover, .header__navbar__link--right:hover {
      color: #232127;
      cursor: pointer; }
    .header__navbar__signup {
      background-color: #2acfcf;
      padding: 10px 15px;
      border: 1px #51dbdb;
      color: white;
      font-size: 16px;
      border-radius: 20px;
      font-weight: 700;
      height: 40px; }

@media (max-width: 678px) {
  .header__navbar {
    flex-direction: column;
    grid-column-start: 2;
    align-items: center;
    margin-bottom: 30px; }
  .header__navbar__link,
  .header__navbar__link--right {
    margin: 7px 0; }
  .Navbar__Items {
    display: none; }
  .Navbar__ToggleShow {
    display: flex; }
  .header__menu {
    grid-column-end: 12;
    display: inline;
    display: initial;
    cursor: pointer;
    margin: 25px 0; }
  .header__navbar__signup {
    padding: 10px 60px;
    font-size: 16px; } }

.Intro {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  text-align: left;
  font-family: "Poppins", sans-serif;
  padding-bottom: 30px; }
  .Intro__body {
    grid-column: 2/7;
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .Intro__title {
    color: #35323e;
    font-weight: 700;
    font-size: 38px;
    margin: 0; }
  .Intro__text {
    color: #9e9aa7;
    font-size: 14px;
    margin-top: 0; }
  .Intro__button {
    background-color: #2acfcf;
    padding: 10px 15px;
    border: 1px #51dbdb;
    color: white;
    font-size: 14px;
    border-radius: 20px;
    font-weight: 700;
    height: 40px;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content; }
  .Intro__img {
    grid-column: 8/-1;
    overflow: hidden;
    margin: 0; }
  .Intro__img > img {
    width: 115%;
    object-fit: cover;
    padding: 10px; }

@media (max-width: 588px) {
  .Intro {
    grid-template-rows: repeat(2, auto);
    text-align: center; }
    .Intro__body {
      grid-column: 2/12;
      grid-row: 2/3;
      align-items: center; }
  .Intro__img {
    grid-column: 2/-1;
    grid-row: 1/2;
    overflow: hidden;
    margin: 0; }
  .Intro__img > img {
    width: 130%;
    padding: 20px;
    object-fit: cover; }
  .Intro__button {
    padding: 10px 25px; } }

